import React from 'react';

import Directory from '../../components/quotes-components/quote-directory/quote-directory.component';
import Header from '../../components/header/header.component'

import './quotes.styles.scss';

class QuoteApp extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            quotes: []
        }
    }

    render() {
        return(
            <div className='container'>
                <Header />
                <Directory />
            </div>
        )
    }
}

export default QuoteApp;